.App {
  text-align: center;
  color: #282c34;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.input {
  width: 50vw;
  max-width: 1000px;
  height: 70vh;
  background-color: floralwhite;
}

.input input {
  margin-top: 40px;
  color: rgba(0, 0, 0, 0.4);
  font-size: 16px;
  width: 60;
}

.circular {
  border-radius: 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.complete {
  border-radius: 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #4caf50;
  border: 0.5px solid #4caf50; /* Green */
  margin: 0px;
  padding: 0px;
}
.linethrough {
  text-decoration: line-through;
  opacity: 0.6;
}

.notcomplete {
  border-radius: 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: 1px solid #4caf50; /* Green */
  background-color: floralwhite;
  margin: 0px;
  padding: 0px;
}

.noline {
  text-decoration: none;
  opacity: 1;
}
.listItem {
  display: inline-block;
}
.listItem li {
  text-align: left;
  white-space: pre-line;
}

ul {
  text-align: left;
  width: 40vw;
  max-width: 300px;
  height: 30vh;
  min-height: 100px;
  margin: 0 auto;
  padding-left: 0px;
}
li {
  text-align: left;
  margin-left: 0px;
  padding-left: 0px;
  margin-top: 3px;
  list-style: none;
}

.visible {
  visibility: visible;
}

.hidden {
  visibility: hidden;
}

.listHolder {
  max-height: 300px;
  overflow-y: scroll;
}

button {
  background-color: #2d4d57;
  color: floralwhite;
  border: none;
  margin: 5px;
  padding: 10px 30px;
  font-size: 0.6em;
}
button:hover {
  background-color: #4caf50;
}
